import { AnimatePresence, m } from 'framer-motion';
import { opacityAnimation } from '@ui/animation/opacity-animation';
import { useTwoFactorQrCode } from '@common/auth/ui/two-factor/requests/use-two-factor-qr-code';
import { useForm } from 'react-hook-form';
import { useConfirmTwoFactor } from '@common/auth/ui/two-factor/requests/use-confirm-two-factor';
import { TwoFactorStepperLayout } from '@common/auth/ui/two-factor/stepper/two-factor-stepper-layout';
import { Trans } from '@ui/i18n/trans';
import { Skeleton } from '@ui/skeleton/skeleton';
import { Form } from '@ui/forms/form';
import { queryClient } from '@common/http/query-client';
import { FormTextField } from '@ui/forms/input-field/text-field/text-field';
import { useDisableTwoFactor } from '@common/auth/ui/two-factor/requests/use-disable-two-factor';
import { usePasswordConfirmedAction } from '@common/auth/ui/confirm-password/use-password-confirmed-action';
import { Button } from '@ui/buttons/button';
export function TwoFactorConfirmationStep(props) {
  const {
    data
  } = useTwoFactorQrCode();
  return <TwoFactorStepperLayout title={<Trans message="Finish enabling two factor authentication." />} description={<Trans message="To finish enabling two factor authentication, scan the following QR code using your phone's authenticator application or enter the setup key and provide the generated OTP code." />}>
      <AnimatePresence initial={false}>
        {!data ? <QrCodeLayout animationKey="svg-skeleton" svg={<Skeleton variant="rect" size="w-full h-full" />} secret={<Skeleton variant="text" className="max-w-224" />} /> : <QrCodeLayout animationKey="real-svg" svg={<div dangerouslySetInnerHTML={{
        __html: data.svg
      }} />} secret={<Trans message="Setup key: :key" values={{
        key: data.secret
      }} />} />}
      </AnimatePresence>
      <CodeForm {...props} />
    </TwoFactorStepperLayout>;
}
function CodeForm({
  onCancel,
  onConfirmed
}) {
  const form = useForm();
  const confirmTwoFactor = useConfirmTwoFactor(form);
  const disableTwoFactor = useDisableTwoFactor();
  const {
    withConfirmedPassword,
    isLoading: confirmPasswordIsLoading
  } = usePasswordConfirmedAction();
  const isLoading = confirmTwoFactor.isPending || disableTwoFactor.isPending || confirmPasswordIsLoading;
  return <Form form={form} onSubmit={values => withConfirmedPassword(() => {
    confirmTwoFactor.mutate(values, {
      onSuccess: () => {
        queryClient.invalidateQueries({
          queryKey: ['users']
        });
        onConfirmed();
      }
    });
  })}>
      <FormTextField required name="code" label={<Trans message="Code" />} autoFocus />
      <div className="mt-24 flex items-center gap-12">
        <Button type="button" variant="outline" disabled={isLoading} onClick={() => {
        withConfirmedPassword(() => {
          disableTwoFactor.mutate(undefined, {
            onSuccess: onCancel
          });
        });
      }}>
          <Trans message="Cancel" />
        </Button>
        <Button type="submit" variant="flat" color="primary" disabled={isLoading}>
          <Trans message="Confirm" />
        </Button>
      </div>
    </Form>;
}
function QrCodeLayout({
  animationKey,
  svg,
  secret
}) {
  return <m.div key={animationKey} {...opacityAnimation}>
      <div className="mb-16 h-192 w-192">{svg}</div>
      <div className="mb-16 text-sm font-medium">{secret}</div>
    </m.div>;
}